<template>
<div class="login-container bg-light">
        <b-row class=" w-100">
            <b-col sm="4" offset-sm="4">
                <b-card>
                    <b-form @submit.prevent="submit()">
                        <h1 class="mb-3 h3">Reset Password</h1>
                        <b-input-group class="mt-3">
                            <template #prepend>
                            <b-input-group-text>
                                <CIcon name="cil-user"/>
                            </b-input-group-text>
                            </template>
                            <b-form-input v-model="info.email" required  type="email" placeholder="Email"></b-form-input>
                        </b-input-group>

                        <b-input-group class="mt-3">
                            <template #prepend>
                            <b-input-group-text>
                                <CIcon name="cil-lock-locked"/>
                            </b-input-group-text>
                            </template>
                            <b-form-input v-model="info.password" required  type="password" placeholder="Password"></b-form-input>
                        </b-input-group>

                        <b-input-group class="mt-3">
                            <template #prepend>
                            <b-input-group-text>
                                <CIcon name="cil-lock-locked"/>
                            </b-input-group-text>
                            </template>
                            <b-form-input v-model="info.password_confirmation" required  type="password" placeholder="Confirm Password"></b-form-input>
                        </b-input-group>

                        <div class="d-flex">
                            <b-button variant="success"  :disabled="showCount" type="submit" class="w-100 mt-3">
                                <span v-if="!loading.login" >Reset Password</span>
                                <b-spinner v-if="loading.login" small type="grow"></b-spinner>
                            </b-button>
                        </div>
                        <b-alert class="mt-2 mb-0 text-center" variant="success" :show="showSuccess">Password reset successfully! <router-link :to="{name:'Login'}">Login</router-link></b-alert>
                    </b-form>
                </b-card>
            </b-col>
        </b-row>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'ForGotPAssword',
    data() {
        return {
            loading:{
                login:false
            },
            showSuccess:false,
            showCount:false,
            count:60,
            info:{
                email:"",
                password:"",
                password_confirmation:"",
            }
        }
    },
    methods: {
        submit() {
            this.loading.login = true
            let objToSend = {
                email:this.info.email,
                password:this.info.password,
                password_confirmation:this.info.password_confirmation,
                token:this.$route.query.token
            }
            axios.post(`api/v1/password/reset-password`, objToSend)
            .then(res=>{
                this.showSuccess = true;
                this.loading.login = false

            })
            .catch(err=>{
                console.log(err)
                this.loading.login = false
            })
        },

    },


}
</script>
<style scoped>
.login-container{
    min-height: 100vh;
    display: flex;
    align-items: center;
} 

</style>